.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(18rem, 1fr));
  grid-gap: 0rem; /* Adjust gap between cards */
  justify-content: center;
  margin: 0 auto;
  max-width: 1200px;
  align-items: center; /* Removed contradiction */
}

.card-image {
  height: 200px; /* Adjust as needed */
  width: 200px; /* Adjust as needed */
  object-fit: cover;
  border-radius: 50%; /* Crop image as circle */
}

.allocHeader {
  background-color: #f8f9fa;
  padding: 20px;
  border-bottom: 1px solid #dee2e6;
  display: grid; /* Removed contradiction */
  justify-content: center;
}

.allocHeader h3 {
  font-size: 1.2rem;
  color: #6c757d;
  margin-bottom: 10px;
  display: flex; /* Removed contradiction */
  justify-content: center;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f9fa;
  padding: 10px 20px;
}

.navbar-brand {
  font-size: 1.5rem;
  color: #212529;
  text-decoration: none;
}

.navbar-nav {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-item {
  margin-right: 10px;
}

.nav-link {
  color: #212529;
  text-decoration: none;
}

.nav-link:hover {
  color: #007bff;
}

.nav-link.active {
  font-weight: bold;
  text-decoration: underline;
}

.allocHeader h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: #212529;
}

.allocHeader select {
  padding: 8px;
  font-size: 1rem;
  border: 1px solid #ced4da;
  border-radius: 4px;
}

.allocHeader select:focus {
  outline: none;
  border-color: #80bdff;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.employees-container {
  background-color: #f8f9fa;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 10px;
  padding: 20px;
  align-items: center;
}

.hovered-employee-card {
  width: 300px;
  margin: 0 auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  height: 200px;
}
.un-hovered-employee-card {
  background-color: #f8f9ff;
  width: 300px;
  margin: 0 auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  height: 200px;
}

.card-content {
  display: flex;
  align-items: center; /* Center content vertically */
  height: 200px;
}

.card-image {
  width: 100px; /* Example image width */
  height: 100px; /* Example image height */
  border-radius: 50%; /* Make image circular */
  margin-right: 20px; /* Adjust spacing between image and text */
}

.card-details {
  flex: 2; /* Allow card details to fill remaining space */
}

.card-title {
  margin-top: 0;
}

.card-text {
  margin-bottom: 0;
}
.btn {
  margin-top: 10px;
}

footer {
  background-color: #e4e7ea;
  height: 100px;
}
footer h3 {
  text-align: center;
  color: #212529;
  margin: 0;
  position: relative;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.teamMember {
  font-size: 1rem;
}

.container {
  font-family: Arial, sans-serif;
  display: flex;
  width: 100%;
}
.teamDetails {
  display: grid;
  align-items: center;
  justify-content: center;
  background-color: #fff; /* white background for team details */
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  height: fit-content;
}
.detail {
  text-align: center;
}
.c {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border: #6c757d;
  border-radius: 10px;
  box-sizing: border-box;
  width: 700px; /* Adjust the width as needed */
  height: fit-content; /* Adjust the height as needed */
  padding-bottom: 20px;
  margin-top: 20px;
  margin-left: 15px;
}

.filter-buttons {
  color: gray;
  display: flex;
  gap: 10px; /* Adjust the gap between buttons as needed */
}

.filter-button {
  color: gray;
  margin-left: 100px;
}
.numbers {
  text-align: center;
}
.member {
  height: fit-content;
  width: 500px;
  text-align: center;
  margin-left: 5px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
  padding-bottom: 15px;
}
